@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
.App {
  text-align: left;
  padding-bottom: 80px;
}
.content {
  padding: 25px;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.ui.button {
  background-color: red;
}

.picGallery {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
}

.picGallery img {
  /* display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center; */
  width: 100%;
  margin: 12px;
  /* border: 3px solid #108886; */
  max-width: 100%;
  box-shadow: 0 4px 8px 0 rgba(49, 214, 247, 0.3), 0 6px 20px 0 rgba(49, 214, 247, 0.29);
  border: 3px solid #fff;
}

.flip-card {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  width: 30%;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  margin: 12px;
}

.flip-card:hover .flip-card-inner {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  
}

.flip-card-inner {
  position: relative;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 100%;
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  margin: 5px;
}

.flip-card-front, .flip-card-back {
  display: -webkit-flex;
  display: flex;
  /* position: absolute; */
  width: 100%;
  height: 100%;
  -webkit-align-items: center;
          align-items: center;
  /* justify-content: space-around; */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flip-card-front {
  background-color: #def7fc;
}

.flip-card-back {
  display: -webkit-flex;
  display: flex;
  background: linear-gradient(to right, #afec98, 50%, #f0f8ed);
  background-repeat: repeat;
  background-size: cover;
  color: #233313;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  box-shadow: 0 4px 8px 0 rgba(49, 214, 247, 0.3), 0 6px 20px 0 rgba(49, 214, 247, 0.29);
}

.flip-card-back p {
  color: #233313;
  vertical-align: center;
  font-size: 1vw;
  padding: 25% 10%;
  text-align: center;
}

.footer-wrapper {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 25px;
  background-color: #bdf7ed
}

.copyright-wrapper {
  font-size: 16px;
}

.social-media-icon-wrapper {
  font-size: 24px;
}

h3 {
  margin-top: 24px;
}

.para {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 25px;
  margin: 8px;
}

.para1, .para2, .para3 {
  padding: 16px;
}

.musicClips {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
